@use '../inc/vars' as *;

.features {
  min-height: 100vh;
  background-color: $green;
  display: flex;
  align-items: center;
  overflow: hidden;
  @media(max-width: $xl){
  
  }
  @media(max-width: $lg){
  
  }
  @media(max-width: $md){
    
  }
  @media(max-width: $sm){
    min-height: auto;
  }
  @media(max-width: $xs){
  
  }
  
  &__content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(50% - 50px);
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      width: 100%;
    }
    @media(max-width: $xs){
    
    }
    
  }
  .container {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 70vh;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      height: auto;
    }
    @media(max-width: $xs){
    
    }
    
  }
  .section {
    &__title {
      position: relative;
      overflow: hidden;
      height: 100%;
      min-height: 280px;
      opacity: 0;
      transform: translateX(-100%) translateY(-20vh);
      transition: all 0.5s;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
        min-height: 220px;
      }
      @media(max-width: $md){
        min-height: 140px;
        
      }
      @media(max-width: $sm){
        min-height: 115px;
        max-width: 350px;
      }
      @media(max-width: $xs){
        
      }
      
    }
    &__title-el {
      position: absolute;
      opacity: 0;
      transform: translateY(100%);
      transition: opacity 0.5s, transform 0.5s;
      display: flex;
      &.active {
        transform: translateY(0%) !important;
        opacity: 1 !important;
      }
      &.active--past {
        transform: translateY(-100%);
        opacity: 0;
      }
    }
  }
  &__images {
    display: flex;
    width: 50%;
    justify-content: flex-end;
    right: 25px;
    position: absolute;
    top: 0;
    height: 100%;
    gap: 5%;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      right: 20px;
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      position: static;
      height: 280px;
      width: 100%;
      margin-bottom: 35px;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__img-container {
    width: 15%;
    display: flex;
    border-radius: 50px;
    overflow: hidden;
    cursor: pointer;
    transition: .5s all;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      width: calc(100%/3);
      border-radius: 25px;
    }
    @media(max-width: $xs){
    
    }
    
    &.active {
      width: 55%;
      flex-shrink: 0;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      .features__img {
        transform: scale(1) translateY(0%);
      }
    }
  }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top center;
    transition: .5s all;
    transform: scale(2) translateY(25%);
  }

  &__text {
    max-width: 400px;
    padding-left: 60px;
    line-height: 1.5;
    margin-left: 90px;
    position: relative;
    opacity: 0;
    transform: translateY(100%);
    transition: all 0.5s;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      margin-left: 45px;
      padding-left: 30px;
      max-width: 500px;
    }
    @media(max-width: $md){
      margin-left: 0px;
      padding-left: 30px;
    }
    @media(max-width: $sm){
      padding-left: 20px;
    }
    @media(max-width: $xs){
    
    }
    
    &::before {
      content: '';
      width: 1px;
      height: 100vh;
      background-color: $text;
      position: absolute;
      left: 0;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
      }
      @media(max-width: $sm){
        height: 100%;
      
      }
      @media(max-width: $xs){
      
      }
      
    }
  }
  &.active {
    .section__title, .features__text {
      opacity: 1;
      transform: translate(0%) !important;
    }
  }
}